form{
    width: 300px;
    height: auto;
    color: wheat;
    text-align: center;
    background-color: #554646;
    padding: 20px 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 20px auto;
    border-radius: 20px;

    button{
        width: 100px;
        border-radius: 30px 10px;
        margin: 20px auto;
    }
    input{
        border-radius: 10px 30px;
        margin: 5px;
    }
}